.card {
  width: 250px;
  background: rgb(31, 31, 31);
  border: 1px solid rgb(255, 255, 255);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px;
}

.img {
  width: 250px;
  height: 250px;
}

.name {
  font-size: 27px;
  font-weight: 700;
}
.description {
  font-size: 22px;
  font-weight: 500;
}
